/* Definições gerais */
body {
    font-family: Arial, sans-serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
  }
  
  /* Container principal */
  .container {
    max-width: 800px;
    margin: 2rem auto;
    padding: 1rem;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
  }
  
  /* Títulos */
  h1, h2 {
    text-align: center;
  }
  
  /* Seções de formulários */
  .form-container, .export-container {
    margin: 2rem 0;
  }
  
  .form-group {
    margin-bottom: 1rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  input {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Botões */
  button {
    display: block;
    width: 100%;
    padding: 0.75rem;
    background: #007BFF;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  button:hover {
    background: #0056b3;
  }
  
  /* Estilo da área de resposta */
  .response {
    margin-top: 1rem;
    padding: 1rem;
    background: #e9ffe9;
    border: 1px solid #b2d8b2;
    border-radius: 4px;
    overflow-x: auto;
  }
  